import { combineReducers } from 'redux'
import counter from './counter'
import countTimer from './countTimer'
/*
  counterreducer
    state = 0
  combineReducers
    state = {
      counter: 0
    }
*/
export default combineReducers({
  counter,
  countTimer
})



